.wcf--image-compare {
  width: 800px;
  height: 600px;
  overflow: hidden;
  position: relative;
}

.wcf--image-compare .slider-left,
.wcf--image-compare .slider-right {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: absolute;
}

.wcf--image-compare .slider-left {
  left: 0;
  width: 0;
}

.wcf--image-compare .slider-img {
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 800px;
  height: 100%;
  position: absolute;
}

.wcf--image-compare-handle {
  background-color: #fff;
  width: 2px;
  height: 100%;
  margin-left: -1px;
  position: absolute;
  cursor: col-resize !important;
  opacity: 0.6;
  content: "";
}

.wcf--image-compare:hover .wcf--image-compare-handle {
  opacity: 1;
}

.wcf--image-compare-handle:after {
  position: absolute;
  content: "";
  width: 32px;
  height: 32px;
  top: 50%;
  left: 0;
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 100%;
  margin: -16px;
}

.wcf--image-compare .slider-caption {
  position: absolute;
  top: 0;
  padding: 10px 16px;
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  margin: 20px;
  font-size: 14px;
}

.wcf--image-compare .slider-caption-right {
  right: 0;
}

.wcf--image-compare .slider-caption-left {
  left: 0;
}

.wcf--image-compare .cmp-btn-wrapper {
  text-align: center;
  margin: 10px;
  right: 0;
  bottom: 0;
  left: 0;
  position: absolute;
  z-index: 1010;
}

.wcf--image-compare .cmp-btn {
  background-color: #fff;
  padding: 10px 12px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: 0;
}

.cmp--align-left .wcf--image-compare {
  margin-left: 0;
}

.cmp--align-center .wcf--image-compare {
  margin: auto;
}

.cmp--align-right .wcf--image-compare {
  margin-left: auto;
}
